import '../css/fonticon/style.css'

import React, { useState } from 'react'
import { ErrorBoundary } from './error';

import { SettingPageStart } from '../localsetting/settingPage';
import { OverlayLogsPlayer } from './logsPlayer';
import { HeaderSimple } from '../header';
import { OverlayMCombatants } from './pve_Overlay';

import { battleStart, battleStop } from '../v4/timer/timer_event';
import { battleEvent, AreaData, devMode } from '..';
import { PvPMain } from './pvp_main';
import { TBD } from '../v4/maindataFormat';

const PvPAreaZoneFL = [
  'the borderland ruins (secure)',
  'seal rock (seize)',
  'the fields of glory (shatter)',
  'onsal hakair (danshig naadam)'
];

const PvPAreaZoneRW = ['hidden gorge'];

const PvPAreaZoneCC = [
  'the palaistra',
  'the volcanic heart',
  'cloud nine',
  'the clockwork castletown',
  'red sans',
  'the bayside battleground'
];

const PvPAreaZoneOt = ["wolves' den pier"];
const encounterZoneGet = (Encounter) => {
  if (PvPAreaZoneFL.indexOf(Encounter.CurrentZoneName.toLowerCase()) !== -1) {
    return 3;
  } else if (PvPAreaZoneRW.indexOf(Encounter.CurrentZoneName.toLowerCase()) !== -1) {
    return 2;
  } else if (PvPAreaZoneCC.indexOf(Encounter.CurrentZoneName.toLowerCase()) !== -1) {
    return 5;
  } else if (PvPAreaZoneOt.indexOf(Encounter.CurrentZoneName.toLowerCase()) !== -1) {
    if (devMode.wolves) {
      return 4;
    }
    return 0;
  } else {
    return 0;
  }
}
let isPrevActive = 'false';
export const DefaultView = () => {
  let [enc, setEnc] = useState({ Encounter: { CurrentZoneName: 'Unknown Area', DURATION: '0' }, Combatant: {}, isActive: false });
  let [settingVisible, settingPage] = useState(false);
  let [logsPageVisible, logsPage] = useState(false);
  let [tbdTime, setTBD] = useState(0);

  window.EncounterState = async (CombatData) => {
    let areaZone = encounterZoneGet(CombatData.Encounter);
    if (areaZone === 0) {
      if (isPrevActive === 'false' && CombatData.isActive === 'true') {
        logsPage(false);
      }
      if (CombatData.isActive === 'true' || isPrevActive === 'true') {
        if (isPrevActive === 'false' && CombatData.isActive === 'true') {
          logsPage(false);
        }
        isPrevActive = CombatData.isActive;
        setEnc(enc = CombatData);
        window.changeTime_Event(CombatData.Encounter.DURATION);
        window.ChangeTotalDPS_Event(Number(CombatData.Encounter.ENCDPS), Number(CombatData.Encounter.deaths));
      }
    }
    else {
      if (areaZone === 4) {
        if (battleEvent.encounterStart && CombatData.isActive === 'false') {
          TBD.resetData = 'PART';
          battleEvent.encounterStart = false;
          battleEvent.reset = true;
          await battleStop('PART');
        }
      }
      if (!battleEvent.encounterStart && CombatData.isActive === 'true') {
        battleEvent.encounterStart = true;
        window.ChangeTotalDPS_Event(0, 0);
      }

      if (!battleEvent.Result_Page && !battleEvent.Engage && CombatData.isActive === 'true') {
        //Force BattleEvent Start
        battleStart(0, '');
      }
    }
  }
  window.TBDState = (TBDTime) => {
    setTBD(tbdTime = TBDTime);
  }
  let areaType = encounterZoneGet(enc.Encounter);
  if (!enc.isActive && areaType === 4) {
    // simple reset
    if (AreaData.Type === 4) {

    }
  }
  if (devMode.sampleType !== -1) {
    AreaData.Type = devMode.sampleType
  }
  if (settingVisible) {
    return (
      <>
        <SettingPageStart setting={settingPage} />
      </>
    )

  }
  else {
    return (
      <div id="dispArea">
        <div id='mainArea'>
          {/*<HeaderAreaDefaultView />*/}
          <ErrorBoundary>
            <HeaderSimple setting={settingPage} logsPage={logsPage} logsPageState={logsPageVisible} />
          </ErrorBoundary>
          <ErrorBoundary>
            {logsPageVisible ? <OverlayLogsPlayer /> : <Overlay CombatData={enc.Combatant} EncounterData={enc.Encounter} isActive={enc.isActive} TBD={tbdTime} />}
          </ErrorBoundary>
        </div>
      </div>
    );
  }
}

const Overlay = (props) => {
    return (
      <>
        {(() => {//AREA CHECK
          switch (AreaData.Type) {
            case 1: //Seal Rock & Onsal Hakair
              return (<><PvPMain tbdTime={props.TBD} area={"fl"} wolves={false} /></>);
            case 2://Hidden Gorge
              return (<><PvPMain tbdTime={props.TBD} area={"rw"} wolves={false} /></>);
            case 3://Border Land Ruins & Fields Of Glory 
              return (<><PvPMain tbdTime={props.TBD} area={"fl"} wolves={false} /></>);
            case 4://Wolves Den Pier
              return (<><PvPMain tbdTime={props.TBD} area={"cc"} wolves={true} /></>)
            case 5://Crystal Conflict Area
              return (<><PvPMain tbdTime={props.TBD} area={"cc"} wolves={false} /></>);
            case 6://Not Use
              return ('');
            default://PvE 
              return (<><OverlayMCombatants Combatant={props.CombatData} Encounter={props.EncounterData} /></>)
          }
        })()
        }
      </>
    );
}






























